/**/

*,
::before,
::after {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

body {
  font-size: 14px;
  font-family: Microsoft YaHei, sans-serif;
  color: #333;
}

a {
  color: #333;
  text-decoration: none;
}
h1, h2, h3, h4, h5, h6{
    font-weight: normal;
}
input,
textarea {
  border: none;
  outline: none;
  resize: none;
  -webkit-appearance: none;
}

ul {
  list-style: none;
}

.f_left {
  float: left;
}

.f_right {
  float: right;
}
.clearfix::before,
.clearfix::after{
    content: "";
    display: block;
    visibility: hidden;
    height: 0;
    line-height: 0;
    clear: both;
}



