body {
  /*background: #f5f5f5;*/
  background: #fff;
  margin: 0;
  padding: 0;
}

#app {
  margin: 0 auto;
}


/*显示的容器 start*/

.ap_container {
  width: 100%;
  padding: 0;
}


/*显示的容器 end*/


/*头部搜索框 start*/

.ap_search {
  position: fixed;
  left: 0;
  top: 0;
  height: 51px;
  width: 100%;
  z-index: 999;
}

.ap_search .ap_search_box {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background: rgba(201, 21, 35, 0);
  position: relative;
}

.ap_search {
  width: 100%;
  padding: 0 5%;
}

.ap_search .ap_search_box input {
  width: 100%;
  height: 70px;
  border-radius: 40px;
  margin-top: 5px;
  padding-left: 30px;
  font-size: 26px;
  opacity: 0.8;
}

/*.ap_search .ap_search_box .search-logo {
  position: relative;
  top: 0;
  left: 0;
  font-size: 20px;
  line-height: 80px;
}*/


/*头部搜索框 end*/

/*中部菜单 start*/
.ap_nav {
  width: 100%;
  height: auto;
  padding: 35px 0;
}

.ap_nav ul {
  width: 100%;
}

.ap_nav ul li {
  width: 33.33%;
  float: left;
}

.ap_nav ul li a {
  display: block;
  width: 80px;
  height: 80px;
  margin: 0 auto;
  border: 1px solid #fff;
  border-radius: 50%;
  /*background-color: red;*/
}

.ap_nav ul li a span {
  font-size: 30px;
  line-height: 50px;
}


.ap_nav ul li:nth-of-type(1) a {
    background-color: #EE846A;
}
.ap_nav ul li:nth-of-type(2) a {
    background-color: #F5CD5E;
}
.ap_nav ul li:nth-of-type(3) a {
    background-color: #63B6FD;
}

.ap_nav ul li p {
  text-align: center;
  padding: 5px 0;
  font: 26px "simsun";
}
/*中部菜单 end*/

/*课程部分 start*/
.ap-course-now {
    width: 100%;
    text-align: center;
}

.ap-course-now .ap-course-font {
    margin-bottom: 20px;
    font-size: 26px;
}

.ap-course-now .ap-course-font i {
    display: inline-block;
    height: 0;
    line-height: 0;
    font-size: 26px;
    color: #18CCBC;
    margin-right: 3px;
}

.ap-course-now .course-img a {
    display: block;
    width: 90%;
    margin: 0 auto;
}

.ap-course-now .course-img img {
    width: 100%;
}

.ap-course-now .ap-course-show {
    display: flex;
    flex-flow:row wrap;
    justify-content: space-between;
    width: 90%;
    margin: 0 auto;
}
.ap-course-now .ap-course-show .item {
    width: 48%;
    margin-top: 15px;
}
.ap-course-now .ap-course-show .item img {
   width: 100%;
}
.ap-course-now .ap-course-show .item p {
  font-size: 26px;
  padding-left: 5px;
  text-align: left;
}
.ap-course-now .ap-course-show .item span {
   display: inline-block;
   font-size: 20px;
   color:#CACACA;
   margin-right: 20px;
}
/*课程部分 end*/

/*底部的菜单 start*/
.ap-bottom-menu {
    position: fixed;
    left: 0;
    bottom: 0;
    display: flex;
    flex-flow:row wrap;
    justify-content: space-between;
    width: 100%;
    height: 88px;
    z-index: 999;
    background-color: #fff;
    border-top: 1px solid #E5E8EE; 
}
.ap-bottom-menu div{
    width: 25%;
    text-align: center;
}
.ap-bottom-menu div a{
    display: inline-block;
   line-height: 60%;
    font-size: 24px;
}

.ap-bottom-menu div a i{
    display: inline-block;
    height: 0;
    font-size: 66px;
}
/*底部的菜单 end*/



